import React, { useEffect, useState } from "react";

import CollapseDataTable from "../../lib/components/table/CollapseDataTable";
import {
  COLUMN_TYPE_NUMBER,
  COLUMN_TYPE_STRING,
} from "../../lib/common/Constants";
import CustomTabs from "../../lib/components/tabs/CustomTabs";

import Button from "@material-ui/core/Button";
import Toast from "../../lib/components/toast/ErrorToast";

import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import SearchIcon from "@material-ui/icons/Search";
import { Formik, Form } from "formik";
import { useHistory } from "react-router-dom";

import { withStyles,createStyles } from "@material-ui/core/styles";
import AppDatePicker from "../../lib/components/datepicker/appDatePicker";
import { default as MuiButton } from '@material-ui/core/Button';
import {  Typography } from "@material-ui/core";


const requestDetailsCol = [
  {
    name: "Location",
    type: COLUMN_TYPE_STRING,
    key: "location",
    id: 1,
  },
  {
    name: "Containers",
    type: COLUMN_TYPE_NUMBER,
    key: "noOfContainers",
    id: 3,
  },
  {
    name: "Truck Types",
    type: COLUMN_TYPE_NUMBER,
    key: "truckType",
    id: 3,
  },
  {
    name: "Amount",
    type: COLUMN_TYPE_STRING,
    key: "amount",
    id: 4,
  },
  {
    name: "Date And Time",
    type: COLUMN_TYPE_STRING,
    key: "dateTime",
    id: 5,
  },
  {
    name: "Expires In",
    type: COLUMN_TYPE_STRING,
    key: "expiryIn",
    id: 6,
  }
 
];

const containerDetailsCol = [
  { name: "Container Number", key: "container_number", id: 1 },
  { name: "Consignee Details", key: "consigneeDetails", id: 2 },
  { name: "ISO Code", key: "iso_code", id: 3 },
  { name: "PickUp  Terminal", key: "pickupLocation", id: 3 },
  { name: "Truck Type", key: "truckType", id: 4 },
  { name: "Truck Weight", key: "truckWeight", id: 5 },
  { name: "Date and Time", key: "deliveryDate", id: 6 },
];
let remoteRequestDetailsUrl = "/ptms/app/api/secure/requestDetails/fetchRequestDetailsSummary?statusCode=PEND";

const searchForm = {
  fromDate: "",
  toDate: "",
  consigneeName: "",
};

const StyledButton= withStyles((theme) =>
createStyles({
    root: {
       color:'#0568AE' ,
       border:"none",
       boxShadow: "none",
       float:"right"
    }
}

)
)(MuiButton);


export default function SearchRequests() {
  const [pstate, setPstate] = useState(0);
  const [requestDetailsUrl, setRequestDetailsUrl] = useState(remoteRequestDetailsUrl);
  const [tabLabels, setTabLabels] = useState(["ALL"]);
  const [tabSelected, setTabSelected] = useState("ALL");
  const [selectedRows, setSelectedRows] = useState([]);
  const [allCount, setAllCount] = useState();
  const [showSmartSearch, setShowSmartSearch] = useState(false);
  const [formvalues] = useState(searchForm);



  const actions = [{ item: 0, tip: "view", color: "#C62926", icon: "add" ,icon1:"minimize"}];
  let fmk;

  let history = useHistory();

 
  const updateCount = (e) => {
    if(e)
    {
     if (e.allCount !== 0){
    var v_allCount =  "(" + selectedRows.length + "/" + e.allCount + " selected)";
    setAllCount(e.allCount);
    }
    else
    {
      var v_allCount ="(0)";
    }

    setTabLabels(["ALL" + v_allCount]);
    }
  };

  const onSearch = (values) => {
   
    //let finalURL=boeDetailsUrl;
    let finalURL =
    "/ptms/app/api/secure/requestDetails/fetchRequestDetailsSummary?toDate=" +
      values.toDate +
      "&fromDate=" +
      values.fromDate 
    setRequestDetailsUrl(finalURL);
    setPstate(pstate + 1);
  };

  const RenderSmartSearch = () => {
    if (showSmartSearch) {
      return (
        <Formik
          initialValues={formvalues}
          enableReinitialize
         
          //onSubmit={save}
        >
          {(formik) => {
            fmk = formik;
            return (
              <>
                <Form autoComplete="off">
                 
                    <Grid container spacing={1} alignItems="flex-end">
                      <Grid item xs={2}>
                    
                        <AppDatePicker
                          name={"fromDate"}
                          id={"fromDate"}
                          label={"From Date"}
                          iconColor="#0568AE"
                        />
                     
                      </Grid>
                      <Grid item xs={2}>
                        <AppDatePicker
                          name={"toDate"}
                          id={"toDate"}
                          label={"To Date"}
                          iconColor="#0568AE"
                        />
                      </Grid>
                      {/* <Grid item xs={3}>
                        <TextField
                          id="consigneeName"
                          label="Consignee name"
                          variant="outlined"
                          InputLabelProps={{
                            shrink: true,
                          }}
                          size="small"
                          onChange={(event) => {
                            formik.values.consigneeName = event.target.value;
                            setMessage(event.target.value)
                           // setConsigneeName(event.target.value);
                          }}
                          value={message}
                        
                         
                        />
                      </Grid> */}
                      <Grid item xs={3}>
                        {/* <Button
                          variant="contained"
                          style={{
                            backgroundColor: "#FF0000",
                            color: "#FFFFFF",
                          }}
                          //color="secondary"
                          type="submit"
                        >
                          Search
                        </Button> */}
                          <Button
                           color="primary"
                           variant="contained"
                    
                          onClick={()=>onSearch(formik.values)} 
                            >
                           Search
                      </Button>
                      </Grid>
                    </Grid>
                
                </Form>
              </>
            );
          }}
        </Formik>
      );
    } else {
      return <></>;
    }
  };

  const actionsOnDataSelect = (selectedRows) => {
    if (tabSelected === "ALL") {
      setTabLabels([
        tabSelected + "(" + selectedRows.length + "/" + allCount + ")selected",
        
      ]);
    }
   
  };

  const removeContainersSelected = (row) => {
    var i = 0;
    while (i < selectedRows.length) {
      if (selectedRows[i].container_number === row.containerList[0].container_number) {
        selectedRows.splice(i, 1);
      } else {
        ++i;
      }
    }
  };

  const addOrRemoveSelectedData = (row, action, element) => {
    var selectedData = [];
    switch (action) {
      case "collapseRowChecked":
        if (row.checked) {
          selectedData = selectedRows;
          selectedData.push(row);
          setSelectedRows(selectedData);
          actionsOnDataSelect(selectedData);
        } else {
          var i = selectedRows.findIndex(
            (e) =>
              e.container_number === row.container_number 
            
            
          );
          selectedRows.splice(i, 1);
          selectedData = selectedRows;

          actionsOnDataSelect(selectedData);
        }

        break;

      case "collapseRowHeaderChecked":
        if (row.checked) {
          removeContainersSelected(row);
          row.containerList.forEach(function (item) {
            selectedData = selectedRows;
            selectedData.push(item);
          });
          setSelectedRows(selectedData);
          actionsOnDataSelect(selectedData);
        } else {
          row.containerList.forEach(function (item) {
            var i = selectedRows.findIndex(
              (e) =>
                e.container_number === item.container_number 
               
            );
            selectedRows.splice(i, 1);
            selectedData = selectedRows;

            actionsOnDataSelect(selectedRows);
          });
        }

        break;

      case "collapseHeaderChecked":
        if (element) {
          row.forEach(function (item) {
            item.containerList.forEach(function (container) {
              selectedData.push(container);
            });
          });
          setSelectedRows(selectedData);
          actionsOnDataSelect(selectedData);
        } else {
          row.forEach(function (item) {
            item.containerList.forEach(function (container) {
              var i = selectedRows.findIndex(
                (e) =>
                  e.container_number === container.container_number
              );
              selectedRows.splice(i, 1);
              selectedData = selectedRows;
            });
          });
          actionsOnDataSelect(selectedData);
        }
        break;

      default:
    }
    console.log("selected Data", selectedRows);
  };

  return (
    <>
    
      <div className="row">
        <div className="col-md-6">
          <Grid container spacing={1} alignItems="flex-end">
            <Grid item>
              <SearchIcon style={{ color: "grey" }} />
            </Grid>
            <Grid item>
              <TextField
                id="input-with-icon-grid"
                label="Search by any keyword"
                InputLabelProps={{ style: { fontSize: 15 } }}
                fullWidth
              />
            </Grid>
          </Grid>
        </div>
        <div className="col-md-6">
          <StyledButton
            onClick={() => {
              setShowSmartSearch(!showSmartSearch);
            }}
          >
            Smart Search
          </StyledButton>
        </div>
      </div>
      <Typography variant="h2">Available Jobs</Typography>
      <br></br>
      <RenderSmartSearch />
      <div className="row">
        <div className="col-md-6">
          <CustomTabs
            labelList={tabLabels}
            onSelected={(e) => {
              if (e === 0) {
                let url = `/ptms/app/api/secure/requestDetails/fetchRequestDetailsSummary`;
                setRequestDetailsUrl(url);
                setTabSelected("ALL");
                setSelectedRows([]);
                setPstate(pstate + 1);
              } 
            }}
          ></CustomTabs>
        </div>

        <div className="col-md-6">
          <Button
            variant="contained"
            color="secondary"         
            style={{ float: "right"}}
            disabled={selectedRows.length === 0}
            className="float-right"
            onClick={() => {
              console.log("clicked selected Data is ", selectedRows);
              history.push("/assignTrucks", { containerData: selectedRows });
            }}
          >
            Assign
          </Button>
        </div>
      </div>

      <div className="row">
        <div className="col">
          <CollapseDataTable
            refresh={pstate}
            tableKeys={requestDetailsCol}
            remote={true}
            remoteUrl={requestDetailsUrl}
            dataRootKey={"elements"}
            chkbox={true}
            actions={actions}
            collapsableTableKeys={containerDetailsCol}
            collapseTableList="containerList"
            keyTest="container_number"
            handleClick={(row, index, action, element) => {
              console.log("inside parent handleclick");
              addOrRemoveSelectedData(row, action, element);
            }}
            countData={(e) => updateCount(e)}
            selectedData={selectedRows}
            groupBy={"summaryRefNo"}
          />
        </div>
      </div>
    </>
  );
}
